import PropTypes from 'prop-types'

const CarImage = ({carImage}) => {
    return(
        <div className="w-0 sm:w-full">
            <img className="w-72 h-36 rounded-lg" alt="No car found" src={carImage}/>
        </div>
    );
};

CarImage.defaultProps = {
    carImage: 'data:image/jpeg;base64,'
};

CarImage.propTypes = {
    carImage: PropTypes.string,
};

export default CarImage;