import Car from './Car.js';
import React from 'react';
import Title from '../Universal/Title.js';

function Teams() {
  const [data, setData] = React.useState(null)
  React.useEffect(() => {
    fetch("/teams")
      .then((res) => res.json())
      .then((data) => setData(data.grid));
  });

  
    return (
        <Title pageTitle="Teams" searchBarText="Search for team or driver...">
          <div className="w-full grid xl:grid-cols-2 lg:gid-cols-1">
          
              {!data ? <p className="text-black text-left pt-4 text-lg">Loading...</p> :  data.map(function(team, index){
                return (
                  <Car carName={team.teamName}
                    drivers={team.drivers}
                    carImage={team.carImage}
                    key={index}></Car>
                  )
                }) 
              } 
          </div>
        </Title>  
    );
  
}

export default Teams;