import PropTypes from 'prop-types'
import CarImage from './CarImage.js'
import Drivers from './Drivers.js'

const Car = ({carName, drivers, carImage}) => {
    return(
        <div className="py-6 cursor-default w-full">
            <div className="rounded-lg shadow-2xl bg-red-600 hover:bg-red-900 pb-4 mx-4">
                <div className="flex px-16 py-8">
                    <div className="invisible sm:visible">
                        <CarImage carImage={carImage}></CarImage>
                    </div>
                    <div className="flex w-full sm:w-3/4 pl-4 items-center justify-center">
                        <div>
                            <p className="font-semibold border-b border-white border-opacity-50 mb-6 leading-8 max-h-8 overflow-visible "><span className="">{carName}</span></p>
                            <Drivers drivers={drivers}></Drivers>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

Car.defaultProps = {
    carName: 'F1 Team',
    drivers: ['Driver 1', 'Driver 2'],
    carImage: 'data:image/jpeg;base64,'
};

Car.propTypes = {
    carName: PropTypes.string,
    carImage: PropTypes.string,
    drivers: PropTypes.array
};

export default Car;